import './App.css';
import '@mantine/core/styles.css';
import { Table, MantineProvider, SimpleGrid, Select, Container, Group, Checkbox, Modal, Input } from '@mantine/core';
import { useEffect, useState } from 'react';
import axios from 'axios';
import classes from './HeaderTab.module.css'
import { DataTable } from 'mantine-datatable';


function App() {

  if (useState === undefined) {
    throw new Error('React.useState is not defined. Please ensure you are using a compatible version of React.');
  }

  const [lessons, setLessons] = useState([]);

  useEffect(() => {
    fetchLessons();
  }, []);

  const fetchLessons = async () => {
    try {
      const response = await axios.get('/api/v1/lessons');
      setLessons(await response.data);
    } catch (error) {
      console.error('Error fetching lessons:', error);
    }
  };


  const handleRowClick = (lesson) => {
    console.log('Row clicked:', lesson);
    return (
      <Modal
        opened={true}
        onClose={() => {}}
        title={`Lesson Details: ${lesson.time}`}
      >
        <TimeInput radius="lg" placeholder="Input component" value={lesson.time} />
        <Input radius="lg" placeholder="Input component" value={lesson.track.name}  />;
        <Checkbox checked={lesson.bell} label="Zvonek" />
        <Input radius="lg" placeholder="Input component" value={lesson.library.name} />
        <Group position="right" mt="md">
          <button onClick={() => console.log('Save changes')}>Uloi</button>
          <button onClick={() => console.log('Cancel changes')}>Vymaza</button>
        </Group>
      </Modal>
    )
  };

  const renderTableRows = () => {
    return lessons.map((lesson) => (
      <Table.Tr key={lesson.id} onClick={() => handleRowClick.bind(this, lesson)}>
        <Table.Td>{lesson.time}</Table.Td>
        <Table.Td>{lesson.track.name}</Table.Td>
        <Table.Td>{lesson.bell}</Table.Td>
        <Table.Td>{lesson.library.name}</Table.Td>
      </Table.Tr>
    ));
  };

  const getTableColumns = () => {
    return [{accessor: 'time', title: 'as', textAlign: 'left'},
            {accessor: 'track', title: 'Zvukov stopa', textAlign: 'left'},
            {accessor: 'bell', title: 'Zvonek', render: (item) => (
              <Checkbox checked={item}></Checkbox>
            )},
            {accessor: 'library', title: 'Kninica', textAlign: 'left'}];
  };

  const getTableData = () => {
    var a =  lessons.map(lesson => ({
      time: lesson.time,
      track: lesson.track.name,
      bell: lesson.bell,
      library: lesson.library.name
    }));
    return a;
  };



  return (
    <div className="App">
      <MantineProvider>

        <div className={classes.header}>
          <Container size="md">
            <Group align="right" position="apart">
              <Select allowDeselect mt="sm" data={['SK', 'CZ', 'HU', 'PL']} />
            </Group>
          </Container>
        </div>

        <div className={classes.content}>
          <Container size="md" style={{ marginTop: '5px' }}>
            <SimpleGrid cols={{ base: 1, sm: 1 }} spacing="md">
              <DataTable
              highlightOnHover
                columns={getTableColumns()}
                records={getTableData()}
                onRowClick={({record, index, event}) => {
                  handleRowClick(record);
                }}></DataTable>
            </SimpleGrid>
          </Container>
        </div>
      </MantineProvider>
    </div>
  );

};

export default App;


